import { useRef, useState, useEffect } from "react";
import { LuckyGrid } from "@lucky-canvas/react";
import Swal from "sweetalert2";
import withReactContent from "sweetalert2-react-content";
import copy from "copy-to-clipboard";

const App = () => {
  const bjimg =
    "https://img.alicdn.com/imgextra/i3/917298378/O1CN01aXaRE92BlAxUT8BPj_!!917298378.jpg";
  const frameimg =
    "https://img.alicdn.com/imgextra/i3/917298378/O1CN01CHuakF2BlAxb7pn2h_!!917298378.png";
  const wingif =
    "https://img.alicdn.com/imgextra/i3/917298378/O1CN01xehpw42BlAxlaOqJ3_!!917298378.gif";
  const bag =
    "https://img.alicdn.com/imgextra/i3/917298378/O1CN01UerpAW2BlAxjrJB70_!!917298378.png";
  const swalpadding = "1.25rem";
  const swalbkgcolor = "#fdefeff5";
  const mySwal = withReactContent(Swal);
  const myLucky = useRef();
  const myLuckyCount = useRef(-1);
  const targetRef = useRef();
  const lineId = useRef();
  const jwtToken = useRef();
  const myPrizesIndex = useRef();
  const myWincode = useRef();
  const [prizesStatus, setprizesStatus] = useState(false);
  const [alertText, setalertText] = useState("參與抽獎 植題為你買單");
  const baseUrl = ".";
  const defaultConfig = {
    speed: 30,
    accelerationTime: 2500,
    decelerationTime: 5000,
  };
  const [prizes, setprizes] = useState([
    {
      x: 0,
      y: 0,
      borderRadius: "6px",
      fonts: [{ text: "加載中", top: "74%", fontSize: "12px" }],
      shadow: "0 6px 0 #ffbec3",
      imgs: [{ src: bag, top: "10%", width: "64%", height: "64%" }],
    },
    {
      x: 1,
      y: 0,
      borderRadius: "6px",
      shadow: "0 6px 0 #ffbec3",
      fonts: [{ text: "加載中", top: "74%", fontSize: "12px" }],
      imgs: [{ src: bag, top: "10%", width: "64%", height: "64%" }],
    },
    {
      x: 2,
      y: 0,
      borderRadius: "6px",
      shadow: "0 6px 0 #ffbec3",
      fonts: [{ text: "加載中", top: "74%", fontSize: "12px" }],
      imgs: [{ src: bag, top: "10%", width: "64%", height: "64%" }],
    },
    {
      x: 2,
      y: 1,
      borderRadius: "6px",
      shadow: "0 6px 0 #ffbec3",
      fonts: [{ text: "加載中", top: "74%", fontSize: "12px" }],
      imgs: [{ src: bag, top: "10%", width: "64%", height: "64%" }],
    },
    {
      x: 2,
      y: 2,
      borderRadius: "6px",
      shadow: "0 6px 0 #ffbec3",
      fonts: [{ text: "加載中", top: "74%", fontSize: "12px" }],
      imgs: [{ src: bag, top: "10%", width: "64%", height: "64%" }],
    },
    {
      x: 1,
      y: 2,
      borderRadius: "6px",
      shadow: "0 6px 0 #ffbec3",
      fonts: [{ text: "加載中", top: "74%", fontSize: "12px" }],
      imgs: [{ src: bag, top: "10%", width: "64%", height: "64%" }],
    },
    {
      x: 0,
      y: 2,
      borderRadius: "6px",
      shadow: "0 6px 0 #ffbec3",
      fonts: [{ text: "加載中", top: "74%", fontSize: "12px" }],
      imgs: [{ src: bag, top: "10%", width: "64%", height: "64%" }],
    },
    {
      x: 0,
      y: 1,
      borderRadius: "6px",
      shadow: "0 6px 0 #ffbec3",
      fonts: [{ text: "加載中", top: "74%", fontSize: "12px" }],
      imgs: [{ src: bag, top: "10%", width: "64%", height: "64%" }],
    },
  ]);
  const [buttons, setbuttons] = useState([
    {
      x: 1,
      y: 1,
      borderRadius: "6px",
      background: "#fee900",
      shadow: "0 6px 0 #f59d00",
      fonts: [
        {
          text: "開始\n抽獎",
          top: "10px",
          fontColor: "#db0214",
          fontSize: "24px",
          lineHeight: "24px",
          fontWeight: "900",
          wordWrap: false,
          lengthLimit: "60%",
        },
      ],
    },
  ]);
  const defaultStyle = {
    background: "#fdefef",
    fontSize: "12px",
    fontColor: "#000",
    top: "78%",
  };
  const [luckygridStyle, setluckygridStyle] = useState({
    position: "absolute",
    top: "45.1%",
    left: "50%",
    transform: "translateX(-50%)",
  });
  const bjimgStyle = {
    position: "relative",
    width: "100%",
  };
  const [alertStyle, setalertStyle] = useState({
    position: "absolute",
    top: "74.4%",
    left: "50%",
    transform: "translateX(-50%)",
    overflow: "hidden",
    textAlign: "center",
  });

  const [alertTxtStyle, setalertTxtStyle] = useState({
    display: "block",
    width: "86%",
    position: "absolute",
    top: "52%",
    left: "50%",
    color: "#fff",
    fontSize: "12px",
    fontWeight: "500",
    borderRadius: "10px",
    transform: "translate(-50%,-50%)",
    textAlign: "center",
    whiteSpace: "nowrap",
    overflow: "hidden",
  });
  const frameimgStyle = {
    width: "100%",
  };
  const luckygridcontainerStyle = {
    position: "relative",
    width: "100%",
    maxWidth: "900px",
    minWidth: "320px",
    margin: "0 auto",
    backgroundColor: "#db0214",
  };
  const searchBtnStyle = {
    width: "100%",
    // maxWidth: "300px",
    height: "50px",
    color: "#fff",
    backgroundColor: "#db0214",
    lineHeight: "50px",
    fontSize: "1.1rem",
    shadow: "0 6px 3 #f59d00",
    borderRadius: "6px",
    textAlign: "center",
    margin: "0 auto",
  };
  const [luckyWidth, setluckyWidth] = useState(260);
  const [luckyHeight, setluckyHeight] = useState(260);

  const screenChangeHandler = () => {
    const ws = Number(targetRef.current.offsetWidth);
    // x / w = xs / ws = 260 / 414 = 628 / 1000
    // xs = ws * x / w = ws  * 628 /1000
    const luckyWidths = (ws * 650) / 1000;
    // const luckyHeights = (ws * 628) / 1000;
    setluckyWidth(luckyWidths);
    setluckyHeight(luckyWidths);
    // fs = ws * s / w = ws * 24 / 414 =ws * 58 / 1000
    const fs = Math.round((ws * 58) / 1000);
    const sd = Math.round((ws * 12) / 1000);
    const tp = Math.round((ws * 29) / 1000);
    const pfs = Math.round((ws * 28) / 1000);
    const afs = Math.round((ws * 31) / 1000);
    const lucktop = Math.round((ws * 1014) / 1000);
    const alttop = Math.round((ws * 1678) / 1000);
    setprizes(
      prizes.map((p) => {
        p.borderRadius = `${tp}px`;
        p.fonts[0].fontSize = `${pfs}px`;
        p.shadow = `0 ${sd}px 0 #ffbec3`;
        return p;
      })
    );
    setbuttons(
      buttons.map((b) => {
        b.borderRadius = `${tp}px`;
        b.shadow = `0 ${sd}px 0 #f59d00`;
        b.fonts[0].top = `${tp}px`;
        b.fonts[0].fontSize = `${fs}px`;
        b.fonts[0].lineHeight = `${fs + 2}px`;
        return b;
      })
    );
    setluckygridStyle({ ...luckygridStyle, top: `${lucktop}px` });
    setalertStyle({ ...alertStyle, top: `${alttop}px` });
    setalertTxtStyle({ ...alertTxtStyle, fontSize: `${afs}px` });
  };

  const getPrizesList = async () => {
    if (prizesStatus) {
      return;
    }
    await fetch(baseUrl + "/prizes", {
      method: "POST",
      mode: "cors",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
      },
    })
      .then((res) => res.json())
      .then((result) => {
        if (!Array.isArray(result.data)) {
          Swal.fire({
            padding: swalpadding,
            position: "center",
            icon: "error",
            title: "你的網路似乎有問題...",
            text: "暫時無法抽獎，請稍候再重試",
            background: swalbkgcolor,
            showConfirmButton: false,
            allowOutsideClick: false,
          });
          setprizesStatus(true);
          return;
        }
        const newprizes = prizes;
        result.data.forEach((item) => {
          if (newprizes[item.Id]) {
            newprizes[item.Id].fonts[0].text = item.Name;
            newprizes[item.Id].imgs[0].src = item.Image;
          }
        });
        setprizes(newprizes);
        setprizesStatus(true);
      })
      .catch(() => {
        Swal.fire({
          padding: swalpadding,
          position: "center",
          icon: "error",
          title: "你的網路似乎有問題...",
          text: "暫時無法抽獎，請稍候再重試",
          background: swalbkgcolor,
          showConfirmButton: false,
          allowOutsideClick: false,
        });
        setprizesStatus(true);
      });
  };

  const setLineID = async () => {
    await Swal.fire({
      padding: swalpadding,
      title: "請輸入你的LINE ID或電話號碼",
      input: "text",
      inputLabel: "1個ID只有1次機會",
      inputValidator: (value) => {
        if (!value || value.length < 5 || !/^\w+$/.test(value)) {
          return "輸入格式有誤!";
        }
      },
      background: swalbkgcolor,
      showConfirmButton: true,
      confirmButtonColor: "#ea5455",
    }).then((input) => {
      if (!input.isConfirmed) return;
      fetch(baseUrl + "/verify", {
        method: "POST",
        mode: "cors",
        headers: {
          Accept: "application/json",
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ line_id: input.value }),
      })
        .then((res) => {
          if (res.status === 500) {
            Swal.fire({
              padding: swalpadding,
              position: "center",
              icon: "error",
              title: "服務器當機中...",
              text: "暫時無法抽獎，請稍候再重試",
              background: swalbkgcolor,
              showConfirmButton: false,
              allowOutsideClick: false,
            });
          } else if (res.status === 400) {
            Swal.fire({
              padding: swalpadding,
              position: "center",
              icon: "error",
              title: "請檢查你的輸入信息是否正確",
              background: swalbkgcolor,
              showConfirmButton: true,
              allowOutsideClick: false,
              confirmButtonColor: "#ea5455",
            }).then(() => {
              setLineID();
            });
          } else if (res.status === 200) {
            return res.json();
          }
          return null;
        })
        .then((resp) => {
          if (resp.success) {
            myLuckyCount.current = resp.data.count;
            lineId.current = input.value;
            window.sessionStorage.setItem("currentlineid", input.value);
            jwtToken.current = resp.data.token;
            window.sessionStorage.setItem("currentjwttoken", resp.data.token);
            Swal.fire({
              padding: swalpadding,
              position: "top",
              icon: "success",
              title: "祝你好運！",
              background: swalbkgcolor,
              showConfirmButton: false,
              timer: 1500,
            });
          }
        })
        .catch((error) => {
          errorhandler("你的網路好像出了點問題，請稍後再試", "error");
        });
    });
  };

  const errorhandler = (error, icons) => {
    myLucky.current.stop();
    if (icons === "info") {
      mySwal.fire({
        padding: swalpadding,
        title: "Sorry",
        text: error,
        background: swalbkgcolor,
        confirmButtonColor: "#ea5455",
        imageUrl:
          "https://img.alicdn.com/imgextra/i3/917298378/O1CN01rYSA2P2BlAxmdQbE3_!!917298378.png",
        imageWidth: 150,
        footer: '<a href="line://">諮詢Anna老師</a>',
      });
      return;
    }
    mySwal.fire({
      padding: swalpadding,
      title: "提示",
      text: error,
      icon: icons,
      background: swalbkgcolor,
      confirmButtonColor: "#ea5455",
      footer: '<a href="line://">諮詢Anna老師</a>',
    });
  };

  const stopTimer = (index) => {
    const d = 1 + Math.round(Math.random() * 6) * 1000;
    setTimeout(() => {
      myLucky.current.stop(index);
    }, d);
  };

  const setLuckyIndex = () => {
    const p = fetch(baseUrl + "/goodluck", {
      method: "POST",
      mode: "cors",
      headers: {
        Accept: "application/json",
        "Content-Type": "application/json",
        Authorization: jwtToken.current,
      },
    });

    const t = new Promise((resolve, reject) => {
      setTimeout(() => {
        reject("请求超时！");
      }, 6000);
    });
    Promise.race([p, t])
      .then((res) => {
        if (res.status === 401) {
          myLucky.current.stop();
          mySwal
            .fire({
              padding: swalpadding,
              title: "提示",
              text: "違規操作，請重新輸入你的LINE ID或電話號碼",
              icon: "error",
              background: swalbkgcolor,
              confirmButtonColor: "#ea5455",
              footer: '<a href="line://">諮詢Anna老師</a>',
            })
            .then(() => {
              setLineID();
            });
          return null;
        } else if (res.status === 400) {
          myLucky.current.stop();
          mySwal
            .fire({
              padding: swalpadding,
              title: "提示",
              text: "請重新輸入你的LINE ID或電話號碼",
              icon: "info",
              background: swalbkgcolor,
              confirmButtonColor: "#ea5455",
              footer: '<a href="line://">諮詢Anna老師</a>',
            })
            .then(() => {
              setLineID();
            });
          return null;
        } else if (res.status === 500) {
          myLucky.current.stop();
          errorhandler("目前參與人數太多，服務器繁忙！請稍後再試", "warning");
          return null;
        }
        return res.json() || res;
      })
      .then((json) => {
        if (json === null) return;
        if (!json.success) {
          return errorhandler(
            "目前參與人數太多，服務器繁忙！請稍後再試",
            "warning"
          );
        }
        if (json.success && !json.data) {
          myLuckyCount.current = 0;
          return errorhandler("你的抽獎次數已用完", "info");
        }
        myPrizesIndex.current = json.data.prezis_id;
        myWincode.current = json.data.win_code;
        stopTimer(json.data.prezis_id);
        myLuckyCount.current--;
      })
      .catch((error) => {
        errorhandler("你的網路好像出了點問題，請稍後再試", "error");
      });
  };

  const auth = () => {
    const slineid = window.sessionStorage.getItem("currentlineid");
    const sjwtToken = window.sessionStorage.getItem("currentjwttoken");
    if (!lineId.current) {
      if (!slineid) {
        setLineID();
        return false;
      }
      lineId.current = slineid;
    }
    if (!jwtToken.current) {
      if (!sjwtToken) {
        setLineID();
        return false;
      }
      jwtToken.current = sjwtToken;
    }
    return true;
  };

  const startHandler = () => {
    if (!auth()) return;
    console.log(myLuckyCount.current);
    if (myLuckyCount.current === 0) {
      errorhandler("你的抽獎次數已用完", "info");
      return;
    }
    myLucky.current.play();
    setLuckyIndex();
  };

  const endHandler = (prize) => {
    if (myWincode.current) {
      setalertText("恭喜你已中獎：" + prize.fonts[0].text);
      Swal.fire({
        padding: swalpadding,
        title: `恭喜中獎了\n<span style="color:red">>>${prize.fonts[0].text}<<</span>`,
        html: `<span>你的中獎碼為:\n<span>${myWincode.current}</span><br/>快聯繫Anna老師領取禮品吧</span>`,
        imageUrl: prize.imgs[0].src,
        allowOutsideClick: false,
        confirmButtonText: "複製中獎碼",
        showDenyButton: true,
        denyButtonColor: "#07b53b",
        showCancelButton: true,
        cancelButtonText: "關閉",
        denyButtonText: "聯繫Anna老師領獎",
        imageWidth: 150,
        background: `#fdefef url(${wingif})`,
        backdrop: `rgba(0,0,123,0.4) url("${wingif}") left top no-repeat`,
        // footer: '<a href="line://">聯繫Anna老師領獎</a>',
      }).then((result) => {
        if (result.isConfirmed) {
          if (copy(myWincode.current)) {
            Swal.fire("成功複製中獎碼", "", "success");
          } else {
            Swal.fire("驗證碼複製失敗，請手動長按複製", "", "info");
          }
        } else if (result.isDenied) {
          copy(myWincode.current);
          window.open("line://");
        }
      });
      return;
    }
    mySwal.fire({
      padding: swalpadding,
      title: prize.fonts[0].text,
      text: "未中獎",
      background: swalbkgcolor,
      confirmButtonColor: "#ea5455",
      imageUrl:
        "https://img.alicdn.com/imgextra/i2/917298378/O1CN01oYLUlV2BlAxl0nzaw_!!917298378.png",
      imageWidth: 150,
      footer: '<a href="line://">有問題？諮詢Anna老師</a>',
    });
  };

  const SearchWincodeHandle = () => {
    Swal.fire({
      title: "中獎碼查詢",
      input: "text",
      inputLabel: "請輸入你的中獎碼",
      inputAttributes: {
        autocapitalize: "off",
      },
      allowOutsideClick: false,
      showCancelButton: true,
      confirmButtonText: "查詢",
      cancelButtonText: "取消",
      showLoaderOnConfirm: true,
      confirmButtonColor: "#ea5455",
      background: swalbkgcolor,
      inputValidator: (value) => {
        if (value.length !== 8 || !/^\w+$/.test(value)) {
          return "請填寫正確的中獎碼";
        }
      },
      preConfirm: (input) => {
        return fetch(baseUrl + "/search", {
          method: "POST",
          mode: "cors",
          headers: {
            Accept: "application/json",
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ win_code: input }),
        })
          .then((response) => {
            if (response.status !== 200) {
              throw new Error("查詢出錯，請檢查你輸入的中獎碼是否正確");
            }
            return response.json();
          })
          .then((response) => {
            if (response.success === false) {
              throw new Error("未查詢到你的中獎碼，請輸入正確中獎碼");
            }
            return response.data;
          })
          .catch((error) => {
            Swal.showValidationMessage(error);
          });
      },
      footer: '<a href="line://">遇到了問題?點擊聯繫Anna老師</a>',
    }).then((result) => {
      console.log(result);
      if (result?.value) {
        const data = result.value;
        Swal.fire({
          title: data.prize_name,
          background: swalbkgcolor,
          allowOutsideClick: false,
          confirmButtonColor: "#ea5455",
          html: `中獎憑證：<span style="color:green">${data.line_id}</span></span>`,
          imageUrl: data.prize_image,
          imageWidth: 150,
          imageAlt: data.prize_name,
          footer: '<a href="line://">請盡快聯繫Anna老師領取</a>',
        });
      }
    });
  };
  getPrizesList();
  useEffect(() => {
    window.addEventListener("resize", screenChangeHandler);

    screenChangeHandler();
    return () => {
      window.removeEventListener("resize", screenChangeHandler);
    };
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [prizesStatus]);

  return (
    <section style={luckygridcontainerStyle}>
      <img src={bjimg} alt="" style={bjimgStyle} ref={targetRef} />
      <div style={luckygridStyle}>
        <LuckyGrid
          width={luckyWidth + "px"}
          height={luckyHeight + "px"}
          rows={3}
          cols={3}
          ref={myLucky}
          prizes={prizes}
          buttons={buttons}
          defaultConfig={defaultConfig}
          defaultStyle={defaultStyle}
          onStart={startHandler}
          onEnd={endHandler}
        ></LuckyGrid>
      </div>
      <div style={alertStyle}>
        <img src={frameimg} alt="" style={frameimgStyle} />
        <span style={alertTxtStyle}>{alertText}</span>
      </div>
      <div style={searchBtnStyle} onClick={SearchWincodeHandle}>
        中獎碼查詢
      </div>
    </section>
  );
};

export default App;
